
import React, {ReactNode} from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import { Table, Tag, Space } from 'antd';
import './TableMatch.scss';

export interface TableMatchProps {
  children?: ReactNode;
  group?: string;
  className?: string;
  verbose?: boolean;
}

interface Player {
  name: string;
  group: string;
}

const tenistas = require("../../data/players.json") as Player[];

export function TableMatch({ children, className, verbose, group }: TableMatchProps) {

  const players = tenistas.filter(tenista => tenista.group == group);
  return (
    <table className="rotated-header">
    <thead>
      <tr>
        <th></th>
        {
          players.map(player => 
            <th>
              <div className="rotated-header-container">
                <div className="rotated-header-content">{player.name}</div>
              </div>
            </th>
          )
        }
      </tr>
    </thead>
    <tbody>
      {
        players.map((player, index) => 
          <tr>
            <td className="row-header">{player.name}</td> 
            <td className={index == 0 ? "invalid" : ""}></td> 
            <td className={index == 1 ? "invalid" : ""}></td> 
            <td className={index == 2 ? "invalid" : ""}></td> 
            <td className={index == 3 ? "invalid" : ""}></td>
          </tr>
        )
      }
    </tbody>
  </table>
  );
}