
import React, {ReactNode} from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import { Table, Tag, Space } from 'antd';

const columns = [
  {
    title: '#',
    dataIndex: 'pos',
    key: 'pos',
  },
  {
    title: 'Tenista',
    dataIndex: 'tenista',
    key: 'tenista',
  },
  {
    title: 'V',
    dataIndex: 'v',
    key: 'v',
  },
  {
    title: 'D',
    key: 'd',
    dataIndex: 'd',
  },
  {
    title: 'GV',
    dataIndex: 'gv',
    key: 'gv',
  },
  {
    title: 'GP',
    dataIndex: 'gp',
    key: 'gp',
  },
  {
    title: 'SG',
    dataIndex: 'sg',
    key: 'sg',
  },
];


export interface TableGroupProps {
  children?: ReactNode;
  group?: string;
  className?: string;
  verbose?: boolean;
}
interface Player {
  name: string;
  group: string;
}

const tenistas = require("../data/players.json") as Player[];

export function TableGroup({ children, className, verbose, group }: TableGroupProps) {
  const data = tenistas.filter(tenista => tenista.group == group).map(tenista => {
    return {
      key: tenista.name,
      tenista: tenista.name,
      pos: 1,
      v: 0,
      d: 0,
      gv: 0,
      gp: 0,
      sg: 0
    }
  });
  console.log(data);
  return (
    <Table columns={columns} dataSource={data} pagination={false} />
  );
}